import { useContext } from "react";
import { useParams } from "react-router-dom"
import { ProductContext } from "../context/ProductContext";
import "../sass/productdetails.scss"
import { Col,Card,Button,Row } from "react-bootstrap";
import {Container} from "react-bootstrap";


const ProductDetails = () => {
    const [product,setProduct] = useContext(ProductContext)
    const {url} = useParams();
    const productitems = product.find(p=>p.id === url);
  return (
    <>
        <div className="productdetails-page">
        <Container className="con-productdetails">
        <Row>
        <Col sm={12} md={6}>
        <Card className="card1-productdetails">
      <Card.Body>
        <Card.Title>{productitems.title}</Card.Title>
        <Card.Text>
         {productitems.desc}
        </Card.Text>
        <Button variant="primary">Go somewhere</Button>
      </Card.Body>
    </Card>
    </Col>
    <Col sm={12} md={6}>
    <Card className="card2-productdetails">
      <Card.Img variant="top" src={productitems.photo} />
      <Card.Body>
        <Card.Title>{productitems.title}</Card.Title>
        <Card.Text>
          <p>aa</p>
          <p>aa</p>
        </Card.Text>
        <Button variant="primary">Go somewhere</Button>
      </Card.Body>
    </Card>
    </Col>
    </Row>
        </Container>
        
        </div>
        </>

  )
}

export default ProductDetails
