import React from 'react'
import { Table,Container,Button} from 'react-bootstrap'
import "../sass/wishlist.scss"
import { useWishlist } from 'react-use-wishlist';
import { useCart } from 'react-use-cart';


const Wishlist = () => {

  const {addItem}=useCart()
  const {isWishlistEmpty,
    items,
    removeWishlistItem}=useWishlist()
  
  return  (
    <div className='cart-page'>
      <Container className='con-cart'>
      <h1>Wishlist</h1>
      <Table bordered  className='cart-table'>
      <thead>
        <tr >
          <th>#</th>
          <th>Photo</th>
          <th>Product name</th>
          <th>Price</th>
          <th>Add to Cart</th>
          <th>Delete</th>
        </tr>
      </thead>
      <tbody>
        
    
{items.map((fd,i)=>(
  
  <tr >
  <td>{i + 1}</td>
  <td><img src={fd.photo} alt="err" width={50} /></td>
  <td>{fd.title}</td>
  <td>{fd.price}$</td>
  <td>
    <Button className='btn-cart' onClick={()=>{addItem(fd)}}>
      Add to cart
    </Button>
  </td>
  <td>
    <Button className='btn-cart1' onClick={()=>{removeWishlistItem(fd)}}>
      <i className="fa-solid fa-trash"></i>
    </Button>
  </td>
</tr>
))}
  
     
        
        
      </tbody>
    </Table>
    
    <Button className='btn-cart-alldata'>Delete All</Button>
      </Container>
    </div>
  )
}

export default Wishlist