import React from 'react'
import { Button } from "react-bootstrap";
import Table from "react-bootstrap/Table";
import DashboardItem from '../../components/DashbosrdItem';
import { connect } from "react-redux";
import { LinkContainer } from "react-router-bootstrap";
import "../../sass/dashboard.scss"
const Dashboard = (props) => {
  return (
    <>
      <h1 className="text-center my-5">Admin Panel</h1>
      <LinkContainer to="/add">
      <Button variant="primary" className="mb-4">Add</Button>
      </LinkContainer>
      <Table className='table-admin'  bordered>
        <thead >
          <tr>
            <th>#</th>
            <th>Photo</th>
            <th>Title</th>
            <th>Description</th>
            <th>Edit</th>
          </tr>
        </thead>
        <tbody className='text-white'>
          {props.pvalue.map((item, i) => {
           return <DashboardItem
           key={i}
              title={item.title}
              desc={item.desc}
              photo={item.photo}
              count={i+1}
              id={item.id}
            />;
          })}
        </tbody>
      </Table>
    </>
  )
}
const mapStateToProps = (state) => {
  return {
    pvalue: state,
  };
};

export default connect(mapStateToProps)(Dashboard);